import { EventEmitter } from "../../../shared/index.js";
import { SyncableSet } from "./syncableSet.js";
export class SyncableObservableSet extends SyncableSet {
    _eventEmitter = new EventEmitter();
    add(value) {
        super.add(value);
        this._eventEmitter.emit("added", value);
        return this;
    }
    clear() {
        super.clear();
        this._eventEmitter.emit("cleared");
    }
    delete(value) {
        const result = super.delete(value);
        if (result) {
            this._eventEmitter.emit("deleted", value);
        }
        return result;
    }
    on(event, callback) {
        this._eventEmitter.on(event, callback);
    }
    once(event, callback) {
        this._eventEmitter.once(event, callback);
    }
    off(event, callback) {
        this._eventEmitter.off(event, callback);
    }
    listenerCount(event, callback) {
        return this._eventEmitter.listenerCount(event, callback);
    }
}
