export function isIterable(input) {
    return input && Symbol.iterator in Object(input) && typeof input !== "string";
}
export function toIterable(input, preferSet = false) {
    if (isIterable(input)) {
        return input;
    }
    return (preferSet ? new Set([input]) : [input]);
}
export function forEachIterable(input, callback) {
    if (isIterable(input)) {
        for (const item of input) {
            callback(item);
        }
    }
    else {
        callback(input);
    }
}
export function mapIterable(input, mapper) {
    const result = [];
    if (isIterable(input)) {
        for (const item of input) {
            result.push(mapper(item));
        }
    }
    else {
        result.push(mapper(input));
    }
    return result;
}
export function filterIterable(input, predicate) {
    const result = [];
    if (isIterable(input)) {
        for (const item of input) {
            if (predicate(item)) {
                result.push(item);
            }
        }
    }
    else {
        if (predicate(input)) {
            result.push(input);
        }
    }
    return result;
}
export function findInIterable(input, predicate) {
    if (isIterable(input)) {
        for (const item of input) {
            if (predicate(item)) {
                return item;
            }
        }
        return undefined;
    }
    else {
        return predicate(input) ? input : undefined;
    }
}
export function hasInIterable(input, expected) {
    if (input instanceof Set) {
        return input.has(expected);
    }
    else if (input instanceof Map) {
        return input.has(expected);
    }
    else if (Array.isArray(input)) {
        return input.includes(expected);
    }
    else if (isIterable(input)) {
        for (const item of input) {
            if (item === expected) {
                return true;
            }
        }
        return false;
    }
    else {
        return input === expected;
    }
}
export function isPrimitiveValue(value) {
    return value === undefined || value === null || (typeof value !== "object" && typeof value !== "function");
}
export function isPromiseLike(value) {
    return value && typeof value.then === "function" && typeof value.catch === "function";
}
