export function createDisposable(disposeFunction, additionalData) {
    if (!disposeFunction) {
        return {
            dispose() { },
            [Symbol.dispose]() { },
            ...additionalData,
        };
    }
    let isDisposed = false;
    return {
        dispose() {
            if (isDisposed)
                return;
            isDisposed = true;
            disposeFunction();
        },
        [Symbol.dispose]() {
            this.dispose();
        },
        ...additionalData,
    };
}
